.popup {
  position: fixed;
  top: 55%;
  left: 58%;
  transform: translate(-50%, -50%);
  width: 100%;
  max-width: 1435px;
  height: 100%;
  max-height: 706px;
  background: #3a3e52;
  z-index: 99;
  padding-top: 120px;
  padding-bottom: 30px;
  margin: 0 15px;
  display: none;
  transform-origin: center;
  @include r(1800) {
    left: 49%;
    @include r(1600) {
      transform: none;
      top: calc( 50% - 350px );
      left: calc( 50% - 740px );
      @include r(1480) {
        max-width: 1200px;
        left: calc( 50% - 620px );
        @include r(1330) {
          max-width: 1100px;
          left: calc( 50% - 570px ); } } } }



  &__bg {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 5;
    background-color: rgba(#2b2d3c, .85);
    display: none; } }
.wrap__popup {
  max-width: 1250px;
  height: 100%;
  max-height: 480px;
  color: #000;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  @include r(1480) {
    max-width: 1050px; } }


// крестик
.wrap__exit-popup {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  button {
    background-color: transparent;
    width: 48px;
    height: 48px;
    position: absolute;
    top: 35px;
    right: 35px;
    @include r(1330) {
      width: 38px;
      height: 38px; } } }

.popup__btn {
  display: block;
  position: absolute;
  border-radius: 2px;
  top: 20px;
  right: 0px;
  width: 100%;
  height: 3px;
  background-color: #fff; }

.popup__btn-left {
  transform: rotate(45deg); }
.popup__btn-right {
  transform: rotate(-45deg); }


.wrap__info {
  width: 210px;
  max-width: 210px; }

.wrap__info-content, .wrap__date-content, .wrap__content-content {
  width: 160px;
  max-width: 160px;
  margin-left: auto;
  span {
    margin-left: 40px;
    @include r(1330) {
      margin-left: 20px; } }
  div {
    margin-top: 30px;
    @include r(1330) {
      margin-top: 20px; } }
  div:first-child {
    margin-top: 40px; } }


.container__info-content, .container__date-content {
  display: inline-block;
  width: 60px;
  max-width: 60px; }

.popup-text {
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0.32px;
  color: #ffffff;
  @include r(1330) {
    letter-spacing: 0px; } }

.wrap__popup-line1 {
  display: flex;
  justify-content: space-between; }


.container__date-content {
  width: 160px;
  max-width: 160px; }

.wrap__date-content {
  width: 290px;
  max-width: 290px; }


.wrap__content-content {
  max-width: 360px;
  width: 360px; }

.wrap__content-content {
  span {
    display: flex;
    flex-direction: column;
    margin-left: 0; } }

.description-text {
  display: flex;
  flex-direction: column;
  margin-top: 0px !important;
  margin-left: 50px;
  input {
    display: inline-block;
    margin-top: 5px;
    width: 176px;
    max-width: 175px;
    background-color: transparent;
    border-bottom: 1px dashed #fff;
    color: #fff;
    font-size: 14px; } }

.wrap__description, .wrap__quantity {
  display: flex; }

.wrap__content-content-description {
  margin-top: 0 !important;
  width: 100%; }

.wrap__quantity {
  input {
    display: inline-block;
    width: 176px;
    max-width: 175px;
    background-color: transparent;
    border-bottom: 1px dashed #fff;
    color: #fff;
    font-size: 14px; } }



.wrap__content-content-quantity {
  margin-top: 0 !important; }

.wrap__content-content .quantity-text {
  margin-top: 0px !important;
  margin-left: 15px; }


.wrap__content-btn {
  margin-top: 35px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  button {
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 0.32px;
    color: #ffffff;
    padding: 12px 28px;
    border-radius: 5px;
    background-color: #292b3a; } }


.wrap__content-btn-info {
  text-decoration: none;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-image: url('../icons/icon-info.svg');
  width: 17px;
  height: 17px;
  display: inline-block;
  margin-right: 22px; }


// 2 линия
.wrap__popup-line2 {
  margin-top: 20px;
  display: flex; }

.wrap__parameters-content {
  width: 205px;
  max-width: 205px;
  margin-left: 48px;
  span {
    display: inline-block;
    margin-left: 40px;
    width: 55px;
    max-width: 55px; }
  div {
    margin-top: 30px; }
  div:first-child {
    margin-top: 40px; }
  a {
    text-align: right;
    margin-right: 0;
    margin-left: 20px;
    position: relative;
    top: 3px; } }


.container__parameters-content {
  display: inline-block;
  width: 65px;
  max-width: 65px; }

.wrap__location {
  padding-left: 160px; }
.wrap__location-content {
  width: 320px;
  max-width: 320px;
  span {
    display: inline-block;
    margin-left: 40px; }
  div {
    margin-top: 30px; } }

.container__location-content {
  display: inline-block;
  width: 70px;
  max-width: 70px; }
.wrap__popup-address {
  display: flex;
  align-items: baseline;
  margin-top: 0 !important; }

.wrap__popup-country, .wrap__popup-state {
  display: block;
  margin-top: 0 !important; }

.wrap__popup-country {
  margin-top: 10px !important; }




