.popup-info2 {
  position: fixed;
  top: 55%;
  left: 55%;
  transform: translate(-50%, -50%);
  width: 100%;
  max-width: 990px;
  height: 100%;
  max-height: 706px;
  background: #3a3e52;
  z-index: 99;
  padding-top: 120px;
  padding-bottom: 30px;
  margin: 0 15px;
  // display: none
  transform-origin: center;
  @include r(1600) {
    left: 49%; }
  .wrap__popup {
    max-width: 700px;
    height: 100%;
    max-height: 480px;
    color: #000;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column; }
  .wrap__popup-line2 {
    margin-top: 100px;
    display: flex;
    justify-content: space-between; } }
.navbar2 {
  @include r(1800) {
    z-index: 6;
    @include r(1600) {
      z-index: 5; } } }
