.section__info1 {
  min-height: 96px;
  box-shadow: 0px 3px 80px 0 rgba(23, 24, 32, 0.2);
  background-color: #2f3242;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: absolute;
  z-index: 7;
  width: 100%; }

.section__info1-icon {
  margin-right: 159px; }



.wrap__navANDcontent {
  display: flex; }

// стили для меню слева
.navbar {
  width: 243px;
  max-width: 243px;
  padding: 0 47px 0 30px;
  box-shadow: 0px 3px 80px 0 rgba(23, 24, 32, 0.2);
  background-color: #2f3242;
  position: relative;
  z-index: 6;
  @include r(1800) {
    z-index: 5; } }
.wrap__nav {
  width: 165px;
  max-width: 165px;
  height: 100vh;
  position: fixed; }
.navbar__menu {
  width: 165px;
  max-width: 165px;
  height: 100vh;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  li {
    margin-bottom: 56px; } }
.menu__list {
  padding-top: 295px; }
.menu__link {
  box-sizing: border-box;
  text-decoration: none;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0.32px;
  color: #ffffff;
  padding-left: 65px;
  position: relative;
  display: inline-block;
  @include r(1560) {
    padding-left: 45px; } }

.menu__link-gamburger::before,
.menu__link-arrowRound::before,
.menu__link-info::before,
.menu__link-setting::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center; }

.menu__link-gamburger::before {
  width: 27px;
  height: 18px;
  background-image: url('../icons/gamburger.png'); }
.menu__link-arrowRound::before {
  width: 30px;
  height: 24px;
  background-image: url('../icons/arrow-around.png'); }
.menu__link-info::before {
  left: 5px;
  width: 12px;
  height: 24px;
  background-image: url('../icons/info.png'); }
.menu__link-setting::before {
  width: 27px;
  height: 27px;
  background-image: url('../icons/setting.png'); }




.menu__bottom {
  margin-bottom: 42px;
  a {
    margin-top: 45px;
    position: relative; } }
.menu__link-arrow::before,
.menu__link-exit::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center; }

.menu__link-arrow::before {
  width: 29px;
  height: 17px;
  background-image: url('../icons/arrow.png'); }

.menu__link-exit::before {
  top: -5px;
  width: 30px;
  height: 30px;
  background-image: url('../icons/exit.png'); }





// контент справа от меню
.content {
  width: 100%;
  min-height: 1080px;
  background-color: #2b2d3c;
  position: relative;
  padding-top: 96px; }

.content__wrap {
  max-width: 1560px;
  margin: 0 auto;
  height: 100%;
  position: relative;
  padding: 0 15px;
  @include r(1800) {
    padding-left: 50px;
    padding-right: 15px; } }


.row__button {
  width: 100%;
  padding-top: 25px;
  margin-bottom: 85px;
  display: flex;
  justify-content: space-between; }


.row__button-btn {
  padding: 13px 28px;
  border-radius: 5px;
  background-color: #3a3e52;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0.32px;
  color: #ffffff;
  margin-right: 30px;
  @include r(1480) {
    padding: 12px 18px;
    font-size: 14px; } }

.btn-filter {
  margin-right: 0;
  padding-left: 47px;
  position: relative;
  &::before {
    content: '';
    position: absolute;
    top: calc( 50% - 9px );
    left: 15px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-image: url('../icons/filter.png');
    width: 17px;
    height: 17px; } }

.btn-delete, .btn-item {
  margin-right: 0; }

.row__button-center {
  padding-right: 90px;
  @include r(1600) {
    padding-right: 0; } }

// блок с заявка
.request {
  display: flex; }
.request-wrap {
  max-width: 1185px;
  width: 100%; }


.request__text-top {
  padding: 0 35px;
  display: flex;
  justify-content: space-between;
  div {
    font-size: 16px;
    letter-spacing: 0.32px;
    color: #9ea3b4; }

  input[type="checkbox"] {
    display: none; }

  label {
    padding-left: 20px; }

  label::before {
    content: '';
    display: inline-block;
    width: 24px;
    height: 25px;
    border-radius: 5px;
    background-color: #3a3e52; }

  input[type="checkbox"]:checked + label::before {
    content: '';
    background-size: 100%;
    background-image: url('../icons/checked.png'); } }


.request__content {
  min-height: 93px;
  width: 100%;
  border-radius: 5px;
  background-color: #3a3e52;
  margin-top: 23px;
  padding-left: 34px;
  padding-right: 50px;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 4px;
    height: 100%;
    background-color: #ffffff; }
  input[type="checkbox"] {
    display: none; }

  label {
    padding-left: 20px; }

  label::before {
    content: '';
    display: inline-block;
    width: 24px;
    height: 25px;
    border-radius: 5px;
    background-color: #fff; }

  input[type="checkbox"]:checked + label::before {
    content: '';
    background-size: 100%;
    background-image: url('../icons/checked.png'); } }


.request__number-1 {
  margin-top: 19px; }




.request__content-title {
  @include r(1500) {
    padding-left: 20px; } }



// выпадающий список
.select-options {
  height: 100%;
  cursor: pointer;
  margin-top: 48px;
  margin-left: 65px;
  position: relative; }
.select-options_list a {
  border-radius: 5px;
  background-color: #3a3e52;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0.32px;
  color: #ffffff;
  text-decoration: none;
  width: 314px;
  max-width: 314px;
  height: 94px;
  max-height: 94px;
  display: flex;
  align-items: center;
  @include r(1600) {
    width: 250px;
    max-width: 250px; }

  &:hover {
    background-color: #53586e; } }
a:active {
  background-color: #53586e !important; }


.tringal {
  display: block;
  position: absolute;
  right: 55%;
  top: calc( 50% - 2px );
  border: 6px solid transparent;
  border-top: 7px solid #fff;
  z-index: 1;
  @include r(1600) {
    right: 45%;
    border: 4px solid transparent;
    border-top: 5px solid #fff; } }







.select__item {
  a {
    padding-left: 35px;
    background-color: #3a3e52;
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 0.32px;
    color: #ffffff;
    text-decoration: none;
    width: 314px;
    max-width: 314px;
    height: 94px;
    max-height: 94px;
    display: flex;
    align-items: center;
    @include r(1600) {
      width: 250px;
      max-width: 250px; }
    &:hover {
      background-color: #53586e; } } }



.select__phone-link {
  padding-left: 45px; }

.select-options-text {
  display: none;
  position: absolute;
  top: 90%;
  left: 0;
  z-index: 2;
  border-radius: 5px;
  overflow: hidden;
  div {
    border-bottom: 1px solid #1e1e1e; }
  div:last-child {
    border-bottom: none; } }







// JS start
.select-options.is-active .select-options-text {
  display: block; }
.select-options.is-active .select__options-active a {
  background-color: #53586e; }


.jsTringal.active {
  border-top: 4px solid #4A4A4A; }


.select__item.remove {
  display: none; }


// JS end
